  /* GENERAL VIEWS */

  .chartName {
    font-size: 18px;
    font-weight: bold;
    background-color: white;
    color: rgb(61, 10, 95);
    border-radius: 10px;
    line-height: 22px;
    text-align: center;
    display: block;
    justify-items: center;
    position: absolute;
  }

  .chartDesc {
    font-size: 16px;
    font-weight: bold;
    color: rgb(61, 10, 95);
    border-radius: 10px;
    line-height: 22px;
    justify-items: center;
    background-color: white;
    display: block;
    font-family: Raleway;
    text-align: center; 
    margin-right: 300px;
  }
  
  /* Charts */
  
  .charts-container {
    display: grid;
    grid-column-gap: 4rem;
    -webkit-column-gap: 4rem;
            column-gap: 4rem;
    grid-row-gap: 2rem;
    row-gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
    margin-bottom: 20px;
    align-items: center;
    margin-left: 350px;
  }
  
  .chart-item:nth-of-type(1) {
    grid-column: 1;
    grid-row: 1;
    font-size: 13px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  }
  
  .chart-item:nth-of-type(2) {
    grid-column: 2/4;
    grid-row: 1;
    font-size: 13px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  }
  
  .chart-item:nth-of-type(3) {
    grid-column: 1/3;
    grid-row: 2;
    font-size: 13px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  }
  
  .chart-item:nth-of-type(4) {
    grid-column: 3;
    grid-row: 2;
    font-size: 13px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  }
  
  .chart-item,
  .chart-item>div {
    height: 200px;
  }
  
  .chart-item p {
    font-weight: 600;
    /* font-size: 1.1rem; */
  }
  
