input, textarea{
    color: black;
    font-size: 15px;
}

.app { -webkit-transition: all .2s ease-in-out; transition: all .2s ease-in-out; }
.app:hover { -webkit-transform: scale(1.1); transform: scale(1.1); }
.BackButton{
    text-decoration: none;
    height: 20px;
    width: 150px;
    background-color: rgba(248, 248, 248, 0.801);
    color: rgb(211, 43, 226);
    font-size: 15px;
    font-weight: bold;
    border-radius: 5px;
}
.BackButton:link, .BackButton:hover, .BackButton:visited , .BackButton:active{
    text-decoration: none;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

p{
    font-size: 20px;
    font-weight: bolder
}
.all-horoscope{
    border-radius: 10px;
    min-height: 600px;
    width: 550px;
    background-color: rgba(184, 176, 184, 0.788) ;
    margin: 0 auto;
}
.horoscope-title{
    font-size: 80px;
    text-shadow:none;
    color: rgb(255, 255, 255);
    font-weight: bolder;
    font-family: 'Luckiest Guy', cursive;
    letter-spacing: 7px;
    margin-top: 10px;
}
.select-button, .horoscop-button {
    margin: 10px;
    height: 40px;
    width: 200px;
    font-size: 20px;
}
.list{
    overflow:scroll ;
    max-height: 150px;
}
#horoscope-title{
    margin-top: 20px;
}
.cloud-title {
    margin: 50px;
    font-size: 80px;
    font-family: 'Luckiest Guy', cursive;
    color: rgb(253, 253, 253);
}
.cloud-desc {
    font-size: 3rem;
    font-weight: bolder;
}
.start-button {
    height: 50px;
    width: 185px;
    font-size: 30px;
    border-radius: 10px;
    background-color: #e2b1ec;
    margin-top: 15px;
}
.cloud {
    animation: fadein 4s;
    -moz-animation: fadein 4s; 
    -webkit-animation: fadein 4s; 
    -o-animation: fadein 4s; 
    -webkit-animation-duration: 4s; 
            animation-duration: 4s;
    border-radius: 15px;
}
@keyframes fadein {
    from {
        opacity:0;
    }
    to {
        opacity:1;
    }
}
@-webkit-keyframes fadein {
    from {
        opacity:0;
    }
    to {
        opacity:1;
    }
}
